body {
  background: #f7f8f8;
  max-width: 100vw;
}

.tile {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px;

  width: 42px;
  height: 59px;
  left: calc(50% - 42px / 2 - 32px);
  top: calc(50% - 59px / 2 - 63.5px);

  background: #ffffff;
  border: 1px solid #e4eced;
  box-sizing: border-box;
  box-shadow: 4px 4px 4px rgba(201, 209, 211, 0.55);
  border-radius: 4px;
  transition: 0.1s;
}

.tile p {
  position: static;
  left: 34.52%;
  right: 34.52%;
  top: 13.56%;
  bottom: 13.56%;

  font-family: Segoe UI;
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 43px;

  color: #40a8bf;

  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 0px 0px;
}

.tile:hover {
  cursor: grab;
  border: 2px solid #bee1e9;
}

.tile:active {
  cursor: grabbing;
  transform: scale(1.4);
  box-shadow: 12px 12px 10px rgba(201, 209, 211, 0.55);
}

.letter-bar {
  width: 100%;
  height: 47px;

  background: #ffffff;
  border: 1px solid #a3aeaf;
  box-sizing: border-box;
  border-radius: 4px;

  font-family: Segoe UI;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 43px;

  color: #6b7678;
}

.shell {
  z-index: 1000;
  position: relative;
  display: inline-block;
}

.container {
  width: 100%;
  /* display: inline-block; */
  position: relative;
  margin: 0 auto;
  border: 1px solid #a3aeaf
}

.app {
  width: 75%;
  margin: 0 auto;
}